import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 <b>.includes()</b>   ,  
				,      .  
				   <b>true</b>,   , {' '}
				<b>false</b>,    .  <b>.includes()</b>{' '}
				,         
				       .
			</p>
			<p> :</p>
			<Examples>
				const array = [1, 2, 3, 4, 5];
				<br />
				const value = 3;
				<br />
				<br />
				const includesValue = array.includes(value);
				<br />
				<br />
				console.log(includesValue); // : true
			</Examples>
			<p> :</p>
			<Examples>
				const str = ' ';
				<br />
				const subStr = '';
				<br />
				<br />
				const includesSubStr = str.includes(subStr);
				<br />
				<br />
				console.log(includesSubStr); // : true
			</Examples>
			<p>
				 <b>.includes()</b>    , , 
				,     ,  
				        
				    .
			</p>
		</div>
	)
}

export default Question
