/* eslint-disable react/jsx-no-comment-textnodes */
import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 (Prototype)  -  ,   JavaScript
				      .   
				JavaScript     ,   .
				         
				  ,     .
			</p>
			<p> ,     JavaScript:</p>
			<p>
				<ol>
					<li>
						<b>  (Prototype Chain): </b>  
						     , JavaScript    
						.      ,   
						    ,       
						   <b>Object.prototype</b>.
					</li>
					<li>
						<b> prototype  :</b>
						,      {' '}
						<b>new</b>,   <b>prototype</b>,  
						   ,     
						.
					</li>
					<li>
						<b> __proto__: </b>    ,
						    . ,    
						    {' '}
						<b>Object.getPrototypeOf()</b>  <b>Object.setPrototypeOf()</b>{' '}
						,    <b>__proto__</b> 
						       -.
					</li>
				</ol>
			</p>
			<p>:</p>
			<Examples>
				//   person
				<br />
				let person = &#123;
				<br />
				&emsp;&emsp;name: 'John',
				<br />
				&emsp;&emsp;age: 30
				<br />
				&#125;;
				<br />
				<br />
				//     person
				<br />
				let personPrototype = &#123;
				<br />
				&emsp;&emsp;greet: function() &#123;
				<br />
				&emsp;&emsp;&emsp;&emsp;return `,  
				$&#123;this.name&#125;!`;
				<br />
				&emsp;&emsp;&#125;
				<br />
				&#125;;
				<br />
				<br />
				//     person
				<br />
				Object.setPrototypeOf(person, personPrototype);
				<br />
				<br />
				//    greet   
				<br />
				console.log(person.greet()); // : ,   John!
			</Examples>
			<p>
				      JavaScript 
				        
				.     -
				  JavaScript.
			</p>
		</div>
	)
}

export default Question
