import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				  JavaScript,  -   
				       
				.    ,    
				 ,     
				,    .
			</p>
			<p>  JavaScript     :</p>
			<h3>1.  :</h3>
			<p>      .</p>
			<p>:</p>
			<Examples>
				const scream = (text) =&gt; text.toUpperCase();
				<br />
				const emphasize = (text) =&gt; `!!! $&#123;text&#125; !!!`;
				<br />
				<br />
				const loudEmphasis = (text) =&gt; emphasize(scream(text));
				<br />
				<br />
				console.log(loudEmphasis('hello')); // : "!!! HELLO !!!"
			</Examples>
			<h3>2.  :</h3>
			<p>
				        
				   .
			</p>
			<p>:</p>
			<Examples>
				const canSwim = &#123;
				<br />
				&emsp;&emsp;swim() &#123;
				<br />
				&emsp;&emsp;&emsp;&emsp;console.log('Swimming!');
				<br />
				&emsp;&emsp;&#125;
				<br />
				&#125;;
				<br />
				<br />
				const canFly = &#123;
				<br />
				&emsp;&emsp;fly() &#123;
				<br />
				&emsp;&emsp;&emsp;&emsp;console.log('Flying!');
				<br />
				&emsp;&emsp;&#125;
				<br />
				&#125;;
				<br />
				<br />
				const swimmingBird = Object.assign(&#123;&#125;, canSwim, canFly);
				<br />
				<br />
				swimmingBird.swim(); // : "Swimming!"
				<br />
				swimmingBird.fly(); // : "Flying!"
			</Examples>
			<h3>3.     :</h3>
			<p>      .</p>
			<p>:</p>
			<Examples>
				const scream = (text) =&gt; text.toUpperCase();
				<br />
				const emphasize = (text) =&gt; `!!! $&#123;text&#125; !!!`;
				<br />
				<br />
				const compose = (...functions) =&gt; (input) =&gt;
				<br />
				&emsp;&emsp;functions.reduce((acc, func) =&gt; func(acc), input);
				<br />
				<br />
				const loudEmphasis = compose(emphasize, scream);
				<br />
				<br />
				console.log(loudEmphasis('hello')); // : "!!! HELLO !!!"
			</Examples>
			<p>
				  JavaScript    
				,      ,
				   ,   
				.        
				 .
			</p>
		</div>
	)
}

export default Question
