import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question_1 = () => {
	return (
		<div>
			<p>
				 <b>&lt;output&gt;</b>  HTML5   
				    ,    
				 - , ,      
				       .
			</p>
			<p>
				     
				      -.  
				       ,  
				  .
			</p>
			<p>
				   <b>&lt;output&gt;</b>:
			</p>
			<Examples>
				&lt;form oninput="result.value = parseInt(a.value) +
				parseInt(b.value)"&gt;
				<br />
				&emsp;&emsp;&lt;input type="number" id="a" name="a" value="0"&gt;
				<br />
				&emsp;&emsp;+ &lt;input type="number" id="b" name="b" value="0"&gt;
				<br />
				&emsp;&emsp;= &lt;output name="result" for="a b"&gt;0&lt;/output&gt;
				<br />
				&lt;/form&gt;
			</Examples>
			<p>
				        <b>a</b>  <b>b</b>, {' '}
				<b>&lt;output&gt;</b>     
				      .
			</p>
			<p>
				 <b>&lt;output&gt;</b>    
				  ,     
				    .
			</p>
		</div>
	)
}

export default Question_1
