import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				  (Scope)   ,   
				    ,   
				 ,       
				 .    ,  
				 ( ,   ..)  .
			</p>
			<p>
				       
				 :
			</p>
			<ol>
				<li>
					<p>
						<b>   (Local Scope): </b>  
						(  )    
						   .  ,    
						   ,     .
					</p>
					<p>     JavaScript:</p>
					<Examples>
						function exampleFunction() &#123;
						<br />
						&emsp;&emsp;var localVar = 10; // localVar   
						 
						<br />
						&emsp;&emsp;console.log(localVar);
						<br />
						&#125;
						<br />
						<br />
						exampleFunction(); //  10
						<br />
						console.log(localVar); // , localVar  
					</Examples>
				</li>
				<li>
					<p>
						<b>   (Global Scope): </b>,
						    ,     
						,     .   
						   .
					</p>
					<p>     JavaScript:</p>
					<Examples>
						var globalVar = 20; // globalVar     function
						anotherFunction() &#123;
						<br />
						&emsp;&emsp;console.log(globalVar); //    globalVar
						  
						<br />
						&#125;
						<br />
						<br />
						anotherFunction(); //  20
					</Examples>
				</li>
			</ol>
		</div>
	)
}

export default Question
