import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 JavaScript    ,  
				 .    :
			</p>
			<p>
				<ol>
					<li>
						<b>  Array.isArray(): </b>  
						   ,     
						 ,    .
						<Examples>
							const arr = [1, 2, 3];
							<br />
							const notArr = 'Hello';
							<br />
							<br />
							console.log(Array.isArray(arr)); // Output: true
							<br />
							console.log(Array.isArray(notArr)); // Output: false
						</Examples>
					</li>
					<li>
						<b>    instanceof: </b>
						  <b>instanceof</b>,  ,
						     (   -
						 <b>Array</b>).
						<Examples>
							const arr = [1, 2, 3];
							<br />
							const notArr = 'Hello';
							<br />
							<br />
							console.log(arr instanceof Array); // Output: true
							<br />
							console.log(notArr instanceof Array); // Output: false
						</Examples>
					</li>
					<li>
						<b>    Object.prototype.toString:</b>
						  <b>toString</b>     
						     <b>[object Array]</b>.
						<Examples>
							const arr = [1, 2, 3];
							<br />
							const notArr = 'Hello';
							<br />
							<br />
							console.log(Object.prototype.toString.call(arr) === '[object
							Array]'); // Output: true
							<br />
							console.log(Object.prototype.toString.call(notArr) === '[object
							Array]'); // Output: false
						</Examples>
					</li>
				</ol>
			</p>
			<p>
				   <b>Array.isArray()</b>  
				,         .
				,     JavaScript    
				,      .
			</p>
		</div>
	)
}

export default Question
