import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 <b>for...of</b>   JavaScript    
				 ,   , ,  (Set), 
				(Map),   ,    .
				        
				     .
			</p>
			<p>
				    <b>for...of</b>:
			</p>
			<ol>
				<li>
					<p>
						<b>  : </b>
						<Examples>
							const fruits = ['', '', ''];
							<br />
							<br />
							for (const fruit of fruits) &#123;
							<br />
							&emsp;&emsp;console.log(fruit);
							<br />
							&#125;
						</Examples>
					</p>
					<p>
						     fruits     .
					</p>
				</li>
				<li>
					<p>
						<b>  :</b>
						<Examples>
							const message = ', !';
							<br />
							<br />
							for (const char of message) &#123;
							<br />
							&emsp;&emsp;console.log(char);
							<br />
							&#125;
						</Examples>
					</p>
					<p>
						      <b>message</b>.
					</p>
				</li>
				<li>
					<p>
						<b>   (Set):</b>
						<Examples>
							const mySet = new Set([1, 2, 3, 4, 5]);
							<br />
							<br />
							for (const item of mySet) &#123;
							<br />
							&emsp;&emsp;console.log(item);
							<br />
							&#125;
						</Examples>
					</p>
					<p>
						<b>for...of</b>     (Set)  
						  .
					</p>
				</li>
				<li>
					<p>
						<b>   (Map):</b>
						<Examples>
							const myMap = new Map([
							<br />
							&emsp;&emsp;['name', 'John'],
							<br />
							&emsp;&emsp;['age', 30]
							<br />
							]);
							<br />
							<br />
							for (const [key, value] of myMap) &#123;
							<br />
							&emsp;&emsp;console.log(key, value);
							<br />
							&#125;
						</Examples>
					</p>
					<p>        (Map).</p>
				</li>
			</ol>
			<p>
				<b>for...of</b>        
				       <b>for</b>,
				      .
			</p>
		</div>
	)
}

export default Question
