/* eslint-disable react/jsx-no-comment-textnodes */
import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 <b>.dataset</b>  JavaScript   
				     (data-) HTML-. 
				      <b>data-*</b>  
				 .
			</p>
			<p>
				  <b>data-*</b>  HTML-    
				 <b>dataset</b>    camelCase.
			</p>
			<p>:</p>
			<p>HTML:</p>
			<Examples>
				&lt;div id="myDiv" data-info="some information"
				data-category="tech"&gt;&lt;/div&gt;
			</Examples>
			<p>JavaScript:</p>
			<Examples>
				const divElement = document.getElementById('myDiv');
				<br />
				<br />
				//   data-   dataset
				<br />
				const info = divElement.dataset.info; // info = 'some information'
				<br />
				const category = divElement.dataset.category; // category = 'tech'
			</Examples>
			<p>
				 <b>.dataset</b>    ,  
				data- HTML-,      
				    JavaScript.  ,  
				data-,  ,    kebab-case 
				camelCase     <b>.dataset</b>.  ,
				 <b>data-info</b>  <b>dataset.info</b>.
			</p>
		</div>
	)
}

export default Question
