import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 JavaScript      (RegExp) 
				 :
			</p>
			<p>
				<ol>
					<li>
						<b>test(): </b> <b>test()</b> ,  
						  .   <b>true</b>, 
						 ,  <b>false</b>,  .
						<Examples>
							let pattern = /hello/;
							<br />
							let text = ', hello!';
							<br />
							let isMatch = pattern.test(text);
							<br />
							console.log(isMatch); // : true
						</Examples>
					</li>
					<li>
						<b>exec(): </b> <b>exec()</b>   
						  .   ,    
						  ;    , {' '}
						<b>null</b>.
						<Examples>
							let pattern = /hello/;
							<br />
							let text = ', hello!';
							<br />
							let result = pattern.exec(text);
							<br />
							console.log(result); // : ['hello', index: 8, input:
							', hello!', groups: undefined]
						</Examples>
					</li>
					<li>
						<b>match(): </b> <b>match()</b>   
						    .   ,
						    ;   , {' '}
						<b>null</b>.
						<Examples>
							let pattern = /hello/;
							<br />
							let text = ', hello!';
							<br />
							let result = text.match(pattern);
							<br />
							console.log(result); // : ['hello']
						</Examples>
					</li>
					<li>
						<b>replace(): </b> <b>replace()</b>  
						        
						.        
						.
						<Examples>
							let pattern = /hello/;
							<br />
							let text = ', hello!';
							<br />
							let newText = text.replace(pattern, 'Hi');
							<br />
							console.log(newText); // : ', Hi!'
						</Examples>
					</li>
					<li>
						<b>search(): </b> <b>search()</b>   
						  .   ,   
						 ;    ,  <b>-1</b>
						.
						<Examples>
							let pattern = /hello/;
							<br />
							let text = ', hello!';
							<br />
							let position = text.search(pattern);
							<br />
							console.log(position); // : 8
						</Examples>
					</li>
				</ol>
			</p>
			<p>
				        
				,   ,     
				 .
			</p>
		</div>
	)
}

export default Question
