import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 (Hoisting) -    JavaScript,  
				       . 
				,     (  
				)        
				,       .  
				    
			</p>
			<Examples>
				console.log(x); // undefined
				<br />
				var x = 10;
			</Examples>
			<p>
				    x  ,     
				  ,     ,  
				undefined
			</p>
			<p> ,   :</p>
			<Examples>
				foo(); // "Hello, world!"
				<br />
				<br />
				function foo() &#123;
				<br />
				&emsp;&emsp;console.log("Hello, world!");
				<br /> &#125;
			</Examples>
			<p>
				  foo     ,  
				    .
			</p>
			<p>
				       ,   
				    
			</p>
		</div>
	)
}

export default Question
