/* eslint-disable react/jsx-no-comment-textnodes */
import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				     JavaScript   
				:
			</p>
			<h3>1.   (ES6 Modules):</h3>
			<p>
				      JavaScript - 
				.     <b>export</b>  <b>import</b> 
				   ,    
				 .
			</p>
			<p>
				    <b>module.js</b>:
			</p>
			<Examples>
				// module.js
				<br />
				export const myFunction = () ={'>'} &#123;
				<br />
				&emsp;&emsp;//  
				<br />
				&#125;;
				<br />
				<br />
				export const myVariable = 42;
			</Examples>
			<p>    :</p>
			<Examples>
				//  
				<br />
				import &#123; myFunction, myVariable &#125; from './module.js';
				<br />
				<br />
				//     
				<br />
				myFunction();
				<br />
				console.log(myVariable);
			</Examples>
			<h3>2.   :</h3>
			<p>
				         
				   ,      
				 (window).
			</p>
			<p>      file1.js:</p>
			<Examples>
				// file1.js
				<br />
				window.myGlobalVariable = 'Hello, world!';
			</Examples>
			<p>      :</p>
			<Examples>
				//  
				<br />
				console.log(window.myGlobalVariable); // : 'Hello, world!'
			</Examples>
			<h3>3.  CommonJS (Node.js):</h3>
			<p>
				     Node.js   , 
				 CommonJS (, Webpack),   
				require()  module.exports        
				.
			</p>
			<p>    module.js (CommonJS):</p>
			<Examples>
				// module.js
				<br />
				const myFunction = () ={'>'} &#123;
				<br />
				&emsp;&emsp;//  
				<br />
				&#125;;
				<br />
				<br />
				module.exports = &#123;
				<br />
				myFunction,
				<br />
				&#125;;
			</Examples>
			<p>    :</p>
			<Examples>
				//  
				<br />
				const &#123; myFunction &#125; = require('./module.js');
				<br />
				<br />
				//   
				<br />
				myFunction();
			</Examples>
			<p>
				        
				,    .  
				JavaScript      
				  (ES6 Modules),     
				    CommonJS   .
			</p>
		</div>
	)
}

export default Question
