import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				<b>.call(), .apply()  bind()</b> -  ,  
				     JavaScript,   
				 -.
			</p>
			<p>
				<ol>
					<li>
						<b>.call():</b>
						<ul>
							<li>
								 <b>call()</b>      
								     ( this), 
								       .
							</li>
							<li>
								: <b>function.call(thisArg, arg1, arg2, ...)</b>.
							</li>
							<li>
								:
								<Examples>
									const person = &#123;
									<br />
									&emsp;&emsp;fullName: function(city, country) &#123;
									<br />
									&emsp;&emsp;&emsp;&emsp;return this.firstName + ' ' +
									this.lastName + ', ' + city + ', ' + country;
									<br />
									&emsp;&emsp;&#125;
									<br />
									&#125;;
									<br />
									<br />
									const person1 = &#123;
									<br />
									&emsp;&emsp;firstName: 'John',
									<br />
									&emsp;&emsp;lastName: 'Doe'
									<br />
									&#125;;
									<br />
									<br />
									const result = person.fullName.call(person1, 'New York',
									'USA');
									<br />
									console.log(result); // Output: John Doe, New York, USA
								</Examples>
							</li>
						</ul>
					</li>
					<li>
						<b>.apply():</b>
						<ul>
							<li>
								<b>apply()</b>   <b>call()</b>,   
								   .
							</li>
							<li>
								: <b>function.apply(thisArg, [argsArray])</b>.
							</li>
							<li>
								:
								<Examples>
									const person = &#123;
									<br />
									&emsp;&emsp;fullName: function(city, country) &#123;
									<br />
									&emsp;&emsp;&emsp;&emsp;return this.firstName + ' ' +
									this.lastName + ', ' + city + ', ' + country;
									<br />
									&emsp;&emsp;&#125;
									<br />
									&#125;;
									<br />
									<br />
									const person1 = &#123;
									<br />
									&emsp;&emsp;firstName: 'John',
									<br />
									&emsp;&emsp;lastName: 'Doe'
									<br />
									&#125;;
									<br />
									<br />
									const args = ['New York', 'USA'];
									<br />
									const result = person.fullName.apply(person1, args);
									<br />
									console.log(result); // Output: John Doe, New York, USA
								</Examples>
							</li>
						</ul>
					</li>
					<li>
						<b>.bind():</b>
						<ul>
							<li>
								 <b>bind()</b>   ,  
								  <b>this</b>    .  
								  ,    , 
								  .
							</li>
							<li>
								: <b>function.bind(thisArg, arg1, arg2, ...)</b>.
							</li>
							<li>
								:
								<Examples>
									const person = &#123;
									<br />
									&emsp;&emsp;fullName: function(city, country) &#123;
									<br />
									&emsp;&emsp;&emsp;&emsp;return this.firstName + ' ' +
									this.lastName + ', ' + city + ', ' + country;
									<br />
									&emsp;&emsp;&#125;
									<br />
									&#125;;
									<br />
									<br />
									const person1 = &#123;
									<br />
									&emsp;&emsp;firstName: 'John',
									<br />
									&emsp;&emsp;lastName: 'Doe'
									<br />
									&#125;;
									<br />
									<br />
									const boundFunc = person.fullName.bind(person1, 'New York',
									'USA');
									<br />
									console.log(boundFunc()); // Output: John Doe, New York, USA
								</Examples>
							</li>
						</ul>
					</li>
				</ol>
			</p>
			<p>
				   <b>call()</b>, <b>apply()</b>  <b>bind()</b>{' '}
				    : <b>call()</b>   
				  , <b>apply()</b>   , {' '}
				<b>bind()</b>      ,  
				  ,    .
			</p>
		</div>
	)
}

export default Question
