/* eslint-disable react/jsx-no-comment-textnodes */
import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 <b>Array.from()</b>  JavaScript    
				     .  
				       .
			</p>
			<p>
				 ,   <b>Array.from()</b>:
			</p>
			<p>
				<ol>
					<li>
						<b>
							      :
						</b>
						<p>
							<b>Array.from()</b>   , {' '}
							<b>arguments, Set, Map, TypedArray</b>   
							   .
						</p>
						<p>:</p>
						<Examples>
							//     
							<br />
							let str = 'Hello';
							<br />
							let charArray = Array.from(str);
							<br />
							console.log(charArray); // : ['H', 'e', 'l', 'l', 'o']
						</Examples>
					</li>
					<li>
						<b> - (map function):</b>
						<p>
							<b>Array.from()</b>     
							      -  
							  .
						</p>
						<p>:</p>
						<Examples>
							//       
							,     2<br />
							let arrayLike = &#123; length: 3, 0: 1, 1: 2, 2: 3 &#125;;
							<br />
							let newArray = Array.from(arrayLike, num =&gt; num * 2);
							<br />
							console.log(newArray); // : [2, 4, 6]
						</Examples>
					</li>
					<li>
						<b>
							 -    
							Symbol.iterator:
						</b>
						<p>
							    <b>Symbol.iterator</b> ( 
							  ), <b>Array.from()</b>   
							   .
						</p>
						<p>:</p>
						<Examples>
							//  -   
							<br />
							let mapper = &#123;
							<br />
							&emsp;&emsp;map: function* () &#123;
							<br />
							&emsp;&emsp;&emsp;&emsp;yield 1;
							<br />
							&emsp;&emsp;&emsp;&emsp;yield 2;
							<br />
							&emsp;&emsp;&emsp;&emsp;yield 3;
							<br />
							&emsp;&emsp;&#125;
							<br />
							&#125;;
							<br />
							<br />
							let newArray = Array.from(mapper.map(), num =&gt; num * 2);
							<br />
							console.log(newArray); // : [2, 4, 6]
						</Examples>
					</li>
				</ol>
			</p>
			<p>
				<b>Array.from()</b>     
				   ,       JavaScript.
			</p>
		</div>
	)
}

export default Question
