import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				 -   CSS   . 
				  :
			</p>
			<ul>
				<li>
					<b>  (Inline CSS): </b>
					   CSS     HTML  
					 style.       
					.
					<Examples>
						&lt;p style="color: blue; font-size: 18px;"&gt;  
						     18 .&lt;/p&gt;
					</Examples>
				</li>
				<li>
					<b>  (Internal CSS): </b>    
					   &lt;head&gt; -   
					&lt;style&gt;.        
					   .
					<Examples>
						&lt;head&gt;
						<br />
						&emsp;&emsp;&lt;title&gt; -&lt;/title&gt;
						<br />
						&emsp;&emsp;&lt;style&gt;
						<br />
						&emsp;&emsp;&emsp;&emsp;p &#123;
						<br />
						&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;color: blue;
						<br />
						&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;font-size: 18px;
						<br />
						&emsp;&emsp;&emsp;&emsp;&#125;
						<br />
						&emsp;&emsp;&lt;/style&gt;
						<br />
						&lt;/head&gt;
						<br />
						&lt;body&gt;
						<br />
						&emsp;&emsp;&lt;p&gt;       
						18 .&lt;/p&gt;
						<br />
						&lt;/body&gt;
						<br />
					</Examples>
				</li>
				<li>
					<b>  (External CSS): </b>   
					 -      .css,  
					  ,      - 
					  &lt;link&gt;.    
					       .
					<p>  styles.css  :</p>
					<Examples>
						p &#123;
						<br />
						&emsp;&emsp;color: blue;
						<br />
						&emsp;&emsp;font-size: 18px;
						<br />
						&#125;
					</Examples>
					<p>
						      &lt;head&gt; 
						HTML-:
					</p>
					<Examples>
						&lt;head&gt;
						<br />
						&emsp;&emsp;&lt;title&gt; -&lt;/title&gt;
						<br />
						&emsp;&emsp;&lt;link rel="stylesheet" href="styles.css"&gt;
						<br />
						&lt;/head&gt;
						<br />
						&lt;body&gt;
						<br />
						&emsp;&emsp;&lt;p&gt;       
						18 .&lt;/p&gt;
						<br />
						&lt;/body&gt;
					</Examples>
				</li>
				<li>
					<b>  @import: </b>    
					    &lt;style&gt;    @import.
					<Examples>
						&lt;head&gt;
						<br />
						&emsp;&emsp;&lt;title&gt; -&lt;/title&gt;
						<br />
						&emsp;&emsp;&lt;style&gt;
						<br />
						@import url("styles.css");
						<br />
						&emsp;&emsp;&lt;/style&gt;
						<br />
						&lt;/head&gt;
						<br />
						&lt;body&gt;
						<br />
						&emsp;&emsp;&lt;p&gt;       
						18 .&lt;/p&gt;
						<br />
						&lt;/body&gt;
					</Examples>
				</li>
			</ul>
		</div>
	)
}

export default Question
