import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				   (Chaining) -   , 
				        (
				  )    , 
				 (.)     .
			</p>
			<p>
				         
				 ,   ,  
				     ,   
				   .
			</p>
			<p>   :</p>
			<Examples>
				const obj = &#123;
				<br />
				&emsp;&emsp;value: 0,
				<br />
				&emsp;&emsp;increment() &#123;
				<br />
				&emsp;&emsp;&emsp;&emsp;this.value++;
				<br />
				&emsp;&emsp;&emsp;&emsp;return this; //    
				  
				<br />
				&emsp;&emsp;&#125;,
				<br />
				&emsp;&emsp;add(num) &#123;
				<br />
				&emsp;&emsp;&emsp;&emsp;this.value += num;
				<br />
				&emsp;&emsp;&emsp;&emsp;return this; //    
				  
				<br />
				&emsp;&emsp;&#125;,
				<br />
				&emsp;&emsp;getValue() &#123;
				<br />
				&emsp;&emsp;&emsp;&emsp;return this.value;
				<br />
				&emsp;&emsp;&#125;
				<br />
				&#125;;
				<br />
				<br />
				const result = obj.increment().add(5).getValue();
				<br />
				console.log(result); // Output: 6
			</Examples>
			<p>
				  <b>increment()</b>  <b>add()</b>  <b>this</b>{' '}
				( ),     . 
				,          
				      
				 .
			</p>
			<p>
				      ,   (
				)      ( ),  
				  ,       .
				     <b>return this;</b>,  <b>this</b>{' '}
				    ( ).
			</p>
		</div>
	)
}

export default Question
