/* eslint-disable react/jsx-no-comment-textnodes */
import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				       
				JavaScript.    :
			</p>
			<h3>
				1.   for...of  forEach()   :
			</h3>
			<Examples>
				//   for...of
				<br />
				const array = ['a', 'b', 'c'];
				<br />
				let obj = &#123;&#125;;
				<br />
				let i = 0;
				<br />
				for (const element of array) &#123;
				<br />
				&emsp;&emsp;obj[i++] = element;
				<br />
				&#125;
				<br />
				console.log(obj); //  : &#123; '0': 'a', '1': 'b',
				'2': 'c' &#125;
			</Examples>
			<h3>2.   reduce()   :</h3>
			<Examples>
				const array = ['a', 'b', 'c'];
				<br />
				const obj = array.reduce((acc, cur, index) =&gt; &#123;
				<br />
				&emsp;&emsp;acc[index] = cur;
				<br />
				&emsp;&emsp;return acc;
				<br />
				&#125;, &#123;&#125;);
				<br />
				console.log(obj); //  : &#123; '0': 'a', '1': 'b',
				'2': 'c' &#125;
			</Examples>
			<h3>3.   Object.fromEntries()  map():</h3>
			<Examples>
				const array = ['a', 'b', 'c'];
				<br />
				const entries = array.map((value, index) =&gt; [index.toString(),
				value]);
				<br />
				const obj = Object.fromEntries(entries);
				<br />
				console.log(obj); //  : &#123; '0': 'a', '1': 'b',
				'2': 'c' &#125;
			</Examples>
			<h3>
				4.    (spread operator)  
				Object.assign():
			</h3>
			<Examples>
				const array = ['a', 'b', 'c'];
				<br />
				const obj = Object.assign(&#123;&#125;, [...array]);
				<br />
				console.log(obj); //  : &#123; '0': 'a', '1': 'b',
				'2': 'c' &#125;
			</Examples>
			<h3>
				5.      key  value  :
			</h3>
			<Examples>
				const array = [<br />
				&emsp;&emsp;&#123; key: 'a', value: 1 &#125;,
				<br />
				&emsp;&emsp;&#123; key: 'b', value: 2 &#125;,
				<br />
				&emsp;&emsp;&#123; key: 'c', value: 3 &#125;
				<br />
				];
				<br />
				<br />
				const obj = array.reduce((acc, &#123; key, value &#125;) =&gt; &#123;
				<br />
				&emsp;&emsp;acc[key] = value;
				<br />
				&emsp;&emsp;return acc;
				<br />
				&#125;, &#123;&#125;);
				<br />
				console.log(obj); //  : &#123; 'a': 1, 'b': 2, 'c':
				3 &#125;
			</Examples>
			<p>
				      ,  
				 JavaScript       
				    /  .
			</p>
		</div>
	)
}

export default Question
