/* eslint-disable react/jsx-no-comment-textnodes */
import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				     DOM-,  {' '}
				<b>addEventListener()</b>.     
				     .  :
			</p>
			<Examples>
				//    ,     
				<br />
				let myElement = document.getElementById('myElementId');
				<br />
				<br />
				//  - 
				<br />
				function myEventHandler(event) &#123;
				<br />
				&emsp;&emsp;//     
				<br />
				&emsp;&emsp;console.log(' !', event);
				<br />
				&#125;
				<br />
				<br />
				//    'click'  
				<br />
				myElement.addEventListener('click', myEventHandler);
			</Examples>
			<p>  :</p>
			<p>
				<ul>
					<li>
						<b>myElementId </b>-   (ID) ,   
						   .
					</li>
					<li>
						<b>myEventHandler </b>-  ,    
						 <b>'click'</b>.
					</li>
					<li>
						<b>addEventListener('click', myEventHandler) </b>- 
						  <b>'click'</b>   <b>myElement</b>.
					</li>
				</ul>
			</p>
			<p>
				   <b>'click'</b>  <b>myElement</b>, 
				 <b>myEventHandler</b>.
			</p>
			<p>
				 <b>addEventListener()</b>     
				,   (, <b>'mouseover'</b>,{' '}
				<b>'keydown'</b>, <b>'submit'</b>  ..).   
				  -,   , 
				    .
			</p>
		</div>
	)
}

export default Question
