import React from 'react'
import { Examples } from '../../Examples/Examples'

const Question = () => {
	return (
		<div>
			<p>
				    (function declaration) 
				  (function expression)   , 
				          (hoisting) 
				 .
			</p>
			<ol>
				<li>
					<b>Function Declaration ( ):</b>
					<p>
						  -    ,  
						         
						,        
						.   - ,  
						      .
					</p>
					<Examples>
						foo(); //    <br />
						function foo() &#123;
						<br />
						&emsp;&emsp;console.log(",   !");
						<br />
						&#125;
					</Examples>
				</li>
				<li>
					<b>Function Expression ( ):</b>
					<p>
						  -     
						  .     
						    ,    .
						       ,  
						 .
					</p>
					<Examples>
						// foo(); // ,    
						<br />
						const foo = function() &#123;
						<br />
						&emsp;&emsp;console.log(",   !");
						<br />
						&#125;;
						<br />
						foo(); //   
					</Examples>
				</li>
			</ol>
			<p>
				       
				      . 
				       
				,         
				 .
			</p>
		</div>
	)
}

